.header {
  height: var(--header-height);
  background-color: grey;
}

.main-content {
  height: calc(100vh - (var(--header-height) + var(--footer-height)));
  overflow: auto;
}

.footer {
  height: var(--footer-height);
  background-color: #fa2a55;
}

.btn {
  background-color: #fa2a55;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: .5rem;
  cursor: pointer;
}